<template>
 <v-container fluid grid-list-md fill-height style="padding:0px 10px 5px 10px;width:100%;height:100%;">
    <v-layout row>
        <v-flex d-flex md4>  
          <v-card> 
            <v-card-title  primary class="title" style="height:5%;width:100%;padding-top:5px;padding-bottom:5px;">Engine Types</v-card-title>
            <v-card-actions  fill-height style="height:95%;width:100%;padding-top:0px;padding-bottom:0px;"> 
              <dx-data-grid
                class="grid" 
                :data-source="engTypesGrid"
                :show-column-lines="true"
                :show-row-lines="true"
                :show-borders="true"
                :word-wrap-enabled="true"
                :row-alternation-enabled="false"
                :column-auto-width="true"
                @cellClick="gridClick"
                @cellPrepared="cellPrepared">
                <dx-column
                  data-field="engType"
                  caption="Engine Type"
                
                />
                <dx-column
                  data-field="controlSys"
                  caption="Control System"
                />
                <dx-column
                  data-field="engCount"
                  caption="Engine Counter"
                />
                <dx-column
                  data-field="shComp"
                  caption="Shipping Companies"
                />
                <!-- 
                  <dx-column
                  data-field="engCount"
                  caption="Engine Counter"
                  cell-template="horGauge"
                  css-class="bullet"
                />
                <dx-column
                  data-field="shComp"
                  caption="Shipping Companies"
                  cell-template="horGauge"
                  css-class="bullet"
                />
                <div
                  slot="horGauge"
                  slot-scope="{ data: cellData }"
                >
                  <hor-gauge 
                    :value="cellData.value"
                    :maxValue="maxValues[cellData.column.dataField]"
                    :minValue="0"
                    :color="'rgb(144,170,218)'"/>
                </div> -->
              </dx-data-grid> 
            </v-card-actions>
          </v-card>         
        </v-flex>
        <v-flex d-flex md4>
          <v-layout column fill-height>
            <v-flex d-flex md6 style="max-height:50%;">
              <Chart v-if="chartData['kpi']"
                v-bind:chartOptions="chartData['kpi']"
                v-bind:title="'Overall Engine KPI / Engine Type'"
                v-bind:active="active"/>  
            </v-flex>
            <v-flex d-flex md6 style="max-height:50%;">
              <Chart v-if="chartData['sfoc']"
                v-bind:chartOptions="chartData['sfoc']"
                v-bind:title="'SFOC / Engine Type​'"
                v-bind:active="active"/>  
            </v-flex>
          </v-layout>
        </v-flex>
        <v-flex d-flex md4>
          <v-layout column fill-height>
            <v-flex d-flex md6 style="max-height:50%;">
              <v-card> 
                <v-card-title  primary class="title" style="height:15%;width:100%;padding-top:5px;padding-bottom:5px;">Most Common Issues / Engine Type</v-card-title>
                <v-card-actions  fill-height style="height:85%;width:100%;padding-top:0px;padding-bottom:0px;"> 
                  <dx-data-grid
                    class="grid" 
                    :data-source="engIssuesGrid"
                    :show-column-lines="true"
                    :show-row-lines="true"
                    :show-borders="true"
                    :word-wrap-enabled="true"
                    :row-alternation-enabled="false"
                    :column-auto-width="true"
                    @cellClick="gridClick"
                    @cellPrepared="cellPrepared">
                    <dx-column
                      data-field="engType"
                      caption="Engine Type"                    
                    />
                    <dx-column
                      data-field="issue1"
                      caption="Issue 1"
                    />
                    <dx-column
                      data-field="issue2"
                      caption="Issue 2"
                    />
                    <dx-column
                      data-field="issue3"
                      caption="Issue 3"
                    />
                  </dx-data-grid> 
                </v-card-actions>
              </v-card>           
            </v-flex>
            <v-flex d-flex md6 style="max-height:50%;">
              <v-layout row>
                <v-flex d-flex md6> 
                  <Chart v-if="chartData['topIssues']"
                    v-bind:chartOptions="chartData['topIssues']"
                    v-bind:title="'Engine types with more issues'"
                    v-bind:active="active"/>
                </v-flex>
                <v-flex d-flex md6>
                  <DropDownChart v-if="chartData['typeIssues']"
                    v-bind:chartOptions="chartData['typeIssues']"
                    v-bind:title="'Top Issues %'"
                    v-bind:dropDownList="engineTypes"
                    v-bind:active="active"/>
                </v-flex>
              </v-layout>  
            </v-flex>
          </v-layout>
        </v-flex>
    </v-layout>
  </v-container>
</template>

<script>

import Chart from '../components/Chart'
import DropDownChart from '../components/DropDownChart'
import HorGauge from '../components/HorGauge'
import { DxDataGrid, DxColumn, DxPaging, DxScrolling} from 'devextreme-vue/data-grid';

  export default {
    name: "EnginesOverview",
    components: {
      Chart,
      DxDataGrid,
      DxColumn,
      DxPaging,
      DxScrolling,
      HorGauge,
      DropDownChart
    },
    data () {
      return {  
        chartData:{},
        fields:['engType','controlSys','engCount','shComp'],
        engineTypes:[],//this.engineTypes:['RT-flex58​','X82','X92​','X62','X72','RT-flex48','RT-flex60','RT-flex84','RT-flex96','X35','X40','RT-flex50DF','RT-flex50','RT-flex68​','X52​','X72 SCR​','X62D','X72DF​'],
        engTypesGrid:[],
        engIssuesGrid:[],
        maxValues:{}    
      
      }
    },
    props: {
      active:Boolean,
      engTypeData:Object
    },
    computed:
    {
    },
    created() {     
    },
    mounted() { 
      //  setTimeout(() => {
      //    this.setData();
      //  });   
      
    },
    methods: { 
      setMaxValues(dataField)
      {
        return Math.max(...this.engTypesGrid.map(function(item){
          return item[dataField];
        }));
      },
      gridClick(e)
      {
        if ((e.column.dataField=="engType")&&(e.rowType!="header"))
        {          
          this.$emit('engSelected',e.value);
        }
      },
      cellPrepared(e)
      {
        if ((e.column.dataField=="engType")&&(e.rowType!="header"))
        {
          e.cellElement.classList.add('clickable');
        }

      },
      setData()
      {
        this.engineTypes = Object.keys(this.engTypeData);

        for (let i=0;i<this.engineTypes.length;i++)
        {
          this.engTypesGrid.push({
            'engType': this.engineTypes[i],
            'controlSys': this.engTypeData[this.engineTypes[i]].controlSystem,
            'engCount': this.engTypeData[this.engineTypes[i]].engineNo,
            'shComp':this.engTypeData[this.engineTypes[i]].companyNo
          });
        }

        this.maxValues['engCount'] = this.setMaxValues('engCount');
        this.maxValues['shComp'] = this.setMaxValues('shComp');

        for (let i=0;i<this.engineTypes.length;i++)
        {
          let obj = {
            'engType': this.engineTypes[i]
          };        

          for (let j=0;j<this.engTypeData[this.engineTypes[i]].common3faults.length;j++)
          {
            obj['issue'+(j+1)]= this.engTypeData[this.engineTypes[i]].common3faults[j] ;
          }
          
          this.engIssuesGrid.push(obj);
        }

        this.$set(this.chartData,'kpi',{
          chart:{
            height:0,
            zoomType: 'xy'
          },
          legend:{
            enabled:false
          },
          xAxis:{
            categories:this.engineTypes,
            title:
            {
              text:'Engine Type',
              align:'high'
            },
            crosshair:true
          },
          yAxis:{
            title:
            {
              text:'[%]',
              align:'high'
            }
            },
          exporting:{
            enabled : false
          },
          title:{
            text:undefined
          },
          series: [{
            type:'column',
            animation: false,
            data:[],
            states: {
              hover: {
                enabled: false
              },
              normal:{
                animation:false
              },
              inactive:{
                opacity: 1
              }
            }
          }]
        });

        for (let i=0;i<this.engineTypes.length;i++)
        {
          let kpi =  this.engTypeData[this.engineTypes[i]].eng_KPI*100;

          let color = '';

          if (this.engTypeData[this.engineTypes[i]].eng_KPI_color==0) color='rgb(60, 171, 48)';
          else if (this.engTypeData[this.engineTypes[i]].eng_KPI_color==10) color='rgb(255, 184, 29)';
          else color='rgb(205, 57, 64)';

          this.chartData['kpi'].series[0].data.push({
              y:kpi,
              color:color});
        }

        this.$set(this.chartData,'sfoc',{
          chart:{
            height:0,
            zoomType: 'xy'
          },
          legend:{
            enabled:false
          },
          xAxis:{
            categories:this.engineTypes,
            title:
            {
              text:'Engine Type',
              align:'high'
            },
            crosshair:true
          },
          yAxis:{
            title:
            {
              text:'[g/kWh]',
              align:'high'
            }
            },
          exporting:{
            enabled : false
          },
          title:{
            text:undefined
          },
          series: [{
            type:'column',
            animation: false,
            data:[],
            states: {
              hover: {
                enabled: false
              },
              normal:{
                animation:false
              },
              inactive:{
                opacity: 1
              }
            }
          }]
        });

        for (let i=0;i<this.engineTypes.length;i++)
        {
          let sfoc =this.engTypeData[this.engineTypes[i]].bsfc;

          this.chartData['sfoc'].series[0].data.push({
              y:sfoc,
              color:'rgb(144,170,218)'
              });
        }

        this.$set(this.chartData,'topIssues',{
          chart:{
            height:0,
          },
          exporting:{
            enabled : false
          },
          title:{
            text:undefined
          },
          plotOptions: {
            pie: {
              shadow: false,
              center: ['50%', '50%'],
              dataLabels: {
                  enabled: false
              },
            }
          },
          series: [{
            type:'pie',
            animation: false,
            data: [],          
            size: '100%',
            innerSize: '70%',
            states: {
              hover: {
                enabled: false
              },
              normal:{
                animation:false
              },
              inactive:{
                opacity: 1
              }
            }
          }]
        });

        for (let i=0;i<this.engineTypes.length;i++)
        {          
          this.chartData['topIssues'].series[0].data.push({
            y:this.engTypeData[this.engineTypes[i]].faultNo,
            name:this.engineTypes[i]
          });
        } 
        
        this.$set(this.chartData,'typeIssues',{});

        for (let i=0;i<this.engineTypes.length;i++)
        {
          this.$set(this.chartData.typeIssues,this.engineTypes[i],{
            chart:{
              height:0,
            },
            exporting:{
              enabled : false
            },
            title:{
              text:undefined
            },
            plotOptions: {
              pie: {
                shadow: false,
                center: ['50%', '50%'],
                dataLabels: {
                    enabled: false
                },
              }
            },
            series: [{
              name:this.engineTypes[i],
              type:'pie',
              animation: false,
              data: [],          
              size: '100%',
              innerSize: '70%',
              states: {
                hover: {
                  enabled: false
                },
                normal:{
                  animation:false
                },
                inactive:{
                  opacity: 1
                }
              }
            }]
          });  
          

          for (let j=0;j<this.engTypeData[this.engineTypes[i]].topFaults.length;j++)
          {    
            //console.log(this.engTypeData[this.engineTypes[i]].topFaults[j]);      
            this.chartData.typeIssues[this.engineTypes[i]].series[0].data.push({
              y:Number(this.engTypeData[this.engineTypes[i]].topFaults[j].percentage),
              name:this.engTypeData[this.engineTypes[i]].topFaults[j].description
            });
          } 

        }   
        

      }
    },
    watch:
    { 
      engTypeData:function()
      {
        setTimeout(() => {
          this.setData();
        });   

      }    
    }

  }

</script>

<style>

.clickable
{
  color:rgb(144,170,218);
  font-weight:600;
  font-size:16px;
  cursor: pointer;
}

.clickable:hover
{
  color:rgb(51,82,128);
}

.grid{
  font-size: 16px;
  font-family:'Roboto', 'sans seriff';
  height:100% !important;
}

</style>
